@use "variables" as v;

/** Media Queries **/
@mixin telefono {
  @media (min-width: v.$telefono) {
    @content;
  }
}
@mixin tablet {
  @media (min-width: v.$tablet) {
    @content;
  }
}
@mixin desktop {
  @media (min-width: v.$desktop) {
    @content;
  }
}
@mixin xl_desktop {
  @media (min-width: v.$xl_desktop) {
    @content;
  }
}

@mixin boton($bgColor, $colorTexto) {
  background-color: $bgColor;
  padding: 1rem 3rem;
  color: $colorTexto;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
  &:hover {
    background-color: darken($bgColor, 10%);
    cursor: pointer;
  }
}

@mixin grid($columnas, $espaciado) {
  display: grid;
  gap: $espaciado;
  grid-template-columns: repeat($columnas, 1fr);
}

@mixin contenedor {
  width: min(95%, 140rem);
  margin: 0 auto;
}

@mixin contenedor-sm {
  width: min(95%, 90rem);
  margin: 0 auto;
}

@mixin headings {
  font-family: v.$fuente_principal;
  font-weight: v.$black;
  margin: 2rem 0;
}

@mixin nombre-pagina {
  @include headings;
  margin: 6rem 0 3rem 0;
  font-weight: v.$delgada;
  text-align: center;
}

@mixin texto-fluido($min, $max) {
  font-size: clamp($min, -0.8rem + 10vw, $max);
}

@mixin texto-degradado($incio, $final) {
  background: linear-gradient(to right, $incio, $final);
  -webkit-background-clip: text;
  color: transparent;
}

@mixin logo {
  color: v.$blanco;
  text-align: center;
  margin: 0;
  @include desktop {
    text-align: left;
    margin-bottom: 0;
  }
}

@mixin menu-social($color, $size) {
  &__enlace {
    &::before {
      color: $color;
      font-size: $size;
      display: inline-block;
      font-style: normal;
      text-rendering: auto;
      -webkit-font-smoothing: antialiased;
      font-family: "Font Awesome 6 Brands";
    }
    &[href*="https://facebook.com"]::before
    {
      content: "\f39e";
    }
    &[href*="https://github.com"]::before
    {
      content: "\f09b";
    }

    &[href*="https://tiktok.com"]::before
    {
      content: "\e07b";
    }
    &[href*="https://instagram.com"]::before
    {
      content: "\f16d";
    }
    &[href*="https://youtube.com"]::before
    {
      content: "\f167";
    }
  }

  &__ocultar {
    display: none;
  }
}

@mixin enlace {
  all: unset;
  color: v.$blanco;
  font-size: 1.4rem;
  text-transform: uppercase;
  font-weight: v.$bold;
  transition: all 0.3s ease;
  &:hover {
    color: v.$azul;
    cursor: pointer;
  }
}

@mixin resetear-lista{
  margin: 0;
  padding: 0;
  list-style: none;
}
